/* AboutMe.css */
.about-me {
  padding: 50px;
  text-align: center;
  background-color: #f8f8f8;
  color: #000000;
}

.about-content {
  display: flex;
  align-items: center;
  justify-content: space-around;
  max-width: 1200px;
  margin: 0 auto;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 1s ease, transform 1s ease;
}

.about-content.show {
  opacity: 1;
  transform: translateY(0);
}

.photo-grid {
  flex: 1;
  margin-right: 20px;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.photo-grid img {
  width: 100%;
  border-radius: 10px;
  transition: transform 0.5s ease;
}

.photo-grid img:hover {
  transform: scale(1.1);
}

.text-content {
  flex: 2;
  text-align: left;
}

.text-content h2,
.text-content h3,
.text-content p {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 1s ease, transform 1s ease;
}

.about-content.show .text-content h2,
.about-content.show .text-content h3,
.about-content.show .text-content p {
  opacity: 1;
  transform: translateY(0);
}

.education-section {
  display: flex;
  justify-content: space-around;
  margin-top: 30px;
}

.education-item {
  text-align: center;
}

.education-info {
  margin-top: 10px;
}

.education-item img {
  width: 100%;
  border-radius: 10px;
  margin-bottom: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
